body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* PAGE */

html {
   line-height: 1.5;
}

body {
  font-size: 1em;
  font-family: Cairo,Arial,sans-serif;
  font-weight: 300;
  letter-spacing: 0;
}

h1, h2, h3 {
  font-family: 'Oswald', 'Arial Narrow', sans-serif;
}

code {
  display: block;
  margin: 20px 0 15px 0;
  padding: 10px;
  background: #eee;
}

.App_market_container {

}

.container {
  box-sizing: border-box;
  width: 100%;
  padding: 45px 20px;
}

.pure-button-primary {
  background-color: #0c1a2b;
}

.pure-button-primary:hover {
  background-color: #233e5e;
}

.pure-form input[type="text"]:focus {
  border-color: #0c1a2b;
}

/* NAVBAR */

.navbar {
  position: fixed;
  padding: 5px;
  background: #0c1a2b;
  font-family: 'Oswald', 'Arial Narrow', sans-serif;
}

.navbar a {
  color: #fff;
}

.navbar a:active,
.navbar a:focus,
.navbar a:hover {
  background: #233e5e;
}

.navbar .pure-menu-heading {
  font-weight: bold;
  text-transform: none;
}

.navbar .navbar-right {
  float: right;
}

.navbar .uport-logo {
  height: 16px;
  margin-right: 10px;
}

#App-loading-screen {
  background-color: #202930;
  height: 100vh;
  width: 100vw;
  color: #9aa3ad;
  font-size: 2rem;
  line-height: 2rem;
  font-weight: 600;
  text-align: center;
  padding-top: 30%;
}
.Market {
	background-color: #202930;
	color: #9aa3ad;
	min-height: 100vh;
	height: 100%;
	width: 100%;
	padding: 3rem 30.5rem 3rem 8rem;
}

.Market-h1 {
	font-size: 3rem;
	line-height: 1;
	letter-spacing: .1px;
	font-weight: 600;
	margin: 0;
}

#Market-title {
	padding-bottom: 2rem;
}

#Market-chart {
	padding-bottom: 2rem;
}

#Market-buysell {
	padding-bottom: 2rem;
}

#Market-stats {
	padding-bottom: 2rem;
}

#Market-activity-pane {
	padding-bottom: 2rem;
}

#Market-orderlists {
	padding-bottom: 2rem;
}

.Market-headers {
	font-size: 1.5rem;
	font-weight: 600;
	line-height: 2rem;
	padding: 0.25rem;
}

.Market-tab-pane {
	padding: 0 !important;
	border: 0 !important;
}

.red {
	color: red;
}

.green {
	color: green;
}

.ui.tabular.menu {
	border: 0px !important;
}

.ui.tabular.menu .item {
	color: white;
	background: #202930;
	border-radius: 0 !important;
	border: 0;
}

.ui.tabular.menu .item:hover {
	color: white;
	background: #111a21;
}

.ui.tabular.menu .active.item {
	background: #0b1215;
	color: white;
	border: 0;
	border-radius: 0 !important;
}

#Market-leaderboard-pane {
	padding-bottom: 2rem;
}

@media (max-width: 1024px) {

}

@media (max-width: 768px) {
  .Market {
  	padding: 6.5rem 1.5rem 0 1.5rem;
  }

  .Market-h1 {
	font-size: 9vw;
	line-height: 0.8;
	letter-spacing: .1px;
	font-weight: 500;
	margin: 0;
  }

  .ui.tabular.menu .active.item {
	width: 33%;
  }

  .ui.tabular.menu .item {
  	width: 33%;
  }

  .Market-orderlist {
 	padding-bottom: 2rem;
  }
}
